import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

const formCreatePromise = document.querySelector('.form');

formCreatePromise.addEventListener('submit', event => {
  event.preventDefault();

  const delay = formCreatePromise.delay.value;
  const state = formCreatePromise.state.value;

  createPromise(delay, state)
    .then(delay => {
      iziToast.success({
        title: 'Success',
        message: `✅ Fulfilled promise in ${delay}ms`,
      });
    })
    .catch(delay => {
      iziToast.error({
        title: 'Error',
        message: `❌ Rejected promise in ${delay}ms`,
      });
    });
  formCreatePromise.reset();
});

function createPromise(delay, state) {
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      if (state === 'fulfilled') {
        resolve(delay);
      } else {
        reject(delay);
      }
    }, delay);
  });
}
